.adsBlock {
	padding: 10px 0 15px;
	clear: both;
	&.light {
		background: $adsGray; }
	&.dark {
		background: $darkGray; }

	.placeholder {
		max-width: 728px;
		margin: 0 auto;
		width: 80%;

		img {
			width: 100%;
			height: auto;
			display: block; } } }


.ads-wide {
	clear: both;
	height: 110px;
	padding: 10px 0;
	display: block;
	@include borderbox;
	@include formobile {
		padding: 10px 0;
		width: 100%;
		height: 70px; }
	&.dark {
		background: $darkGray; }
	&.light {
		background: $adsGray; } }

.ads-box {
	width: 728px;
	height: 90px;
	margin: 0 auto;

	@include formobile {
		width: 100%;
		max-width: 320px;
		height: 50px; }

	&:after {
		content: 'LEADERBOARD';
		display: block;
		width: 100%;
		height: 90px;
		line-height: 90px;
		text-align: center;
		color: $darkGrayDarken;
		border: 1px $darkGrayDarken solid;
		@include formobile {
			height: 50px;
			line-height: 50px; } } }

.ads-content {
	height: auto;
	width: 100%;
	padding: 10px 0;
	@include fortablet {
		width: 100%;
		max-width: 320px;
		height: auto;
		margin: 0 auto; }
	@include formobile {
		padding: 10px 0;
		width: 100%;
		height: auto; }

	.ads-box-content {
		width: 100%;
		max-width: 728px;
		height: 90px;
		margin: 0 auto;
		position: relative;
		line-height: 90px;
		text-align: center;
		color: $white;
		background: $darkGrayDarken;
		font-size: 60px;
		font-family: $opensans;
		font-weight: 900;
		@include fortablet {
			width: 100%;
			height: 50px;
			font-size: 20px;
			line-height: 50px; }
		@include formobile {
			width: 100%;
			max-width: 320px;
			height: 50px;
			line-height: 50px;
			font-size: 20px; }

		&:after {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			z-index: -1;
			left: 0;
			top: 0;
			@include formobile {
				height: 50px;
				line-height: 50px; } } } }
