.showcontswrapper {
	.sideAds {
		img {
			padding: 25px 0; } } }
.showContsWrap {
	.showsTab {
		clear: both;
		color: $white;
		text-transform: uppercase;
		font-size: 14px;
		padding: 5px 0 10px;
		ul {
			overflow: hidden;
			max-width: 1198px;
			margin: 0 auto;
			width: 100%;
			padding: 0 10px;
			position: relative;
			box-sizing: border-box;
			min-width: 320px;
			@include fortablet {
				padding: 0 20px; }
			li {
				float: left;
				p {
					width: 70px;
					line-height: 21px;
					padding-top: 3px;
					display: block; }
				a {
					color: $white;
					text-align: center;
					line-height: 32px;
					padding: 3px 15px 0;
					display: block;
					@include transitionspeed(.3s);
					&:hover {
						background: $red;
						color: $white; } }
				&.active {
					a {
						background: $red;
						color: $white; }
					&:hover {
						a {
							background: $red;
							color: $white; } } }
				&:hover {
					a {
						color: $red;
						background: none; } } } }
		.showConts {
			.scwrap {
				max-width: 1320px;
				margin: 0 auto;
				width: 100%;
				padding: 0 10px;
				position: relative;
				box-sizing: border-box;
				min-width: 320px;
				overflow: hidden;
				@include fortablet {
					padding: 0 20px; }


				.leftPanel2 {
					float: left;
					@include calcu(max-width,'100% - 33%');
					width: 100%;
					@include forresponsive() {
						max-width: none;
						width: 100%;
						clear: both; }
					.holder {
						img {
							width: 100%;
							height: auto; } } }

				.rightPanel2 {
					float: right;
					max-width: 490px;
					min-width: 300px;
					width: 33%;
					@include forresponsive() {
						max-width: none;
						width: 100%;
						clear: both; }

					.scLinks {
						background: $gray;
						width: 100%;
						overflow: hidden;
						&:nth-child(odd) {
							background: $white3; }
						&.scActive {
							background: $red;
							.holder {
								max-width: 390px;
								width: 100%;
								height: 33%;
								height: auto;
								img {
									width: 100%;
									height: auto; }
								.scaconts {
									padding: 10px 10px 10px 20px;
									h2 {
										font-size: 18px;
										line-height: 21px;
										span {
											display: block;
											padding: 3px 0 0; } }
									p {
										@include font($lato, 12px, normal);
										text-transform: none;
										line-height: 18px; } }

								.scawrap {
									float: left;
									width: 100%;
									padding: 10px 0 10px 20px;
									box-sizing: border-box;
									@include fordesktopSmall {
										padding: 10px 5px 7px 20px; }
									.imgBlock {
										float: left;
										@include calcu(max-width,'100% - 80px');
										@include fordesktopSmall {
											@include calcu(max-width,'100% - 150px'); } }

									.socialIcons {
										float: right;
										@include fordesktopSmall {
											margin-right: 10px; }
										@include forresponsive() {
											margin-right: 10px; }
										a {
											display: block;
											color: $white;
											width: 55px;
											height: 55px;
											position: relative;
											margin-bottom: 3px;
											@include transitionspeed(.3s);
											&.fbLink {
												background: $fbBlue;
												&:hover {
													background: $fbBlueDarken; } }
											&.twLink {
												background: $twBlue;
												&:hover {
													background: $twBlueDarken; } }
											i {
												@include centerAll(); } } } } } }
						&.scNormal {
							.holder {
								overflow: hidden;
								padding: 5px 0 4px;
								@include fordesktopSmall {
									padding: 1px 0px 0; }
								div {
									width: 36%;
									margin-right: 2%;
									float: left;
									padding: 10px 0 10px 20px;

									&:last-of-type {
										margin-right: 0; }
									&.imgBlock {
										@include fordesktopSmall {
											width: 25%; }
										@include fortablet {
											width: 100%;
											max-width: 160px;
											margin: 0 auto;
											float: none;
											padding: 10px 0; }
										img {
											width: 100%;
											height: auto; } }
									&.scaconts {
										padding: 10px 10px 10px 20px;
										@include fordesktopSmall {
											padding: 10px 10px 10px 5px;
											width: 62%; }
										@include fortablet {
											width: 100%;
											box-sizing: border-box;
											padding: 20px; }
										h2 {
											font-size: 18px;
											line-height: 21px;
											span {
												display: block;
												padding: 3px 0 0; } }
										p {
											@include font($lato, 12px, normal);
											text-transform: none;
											line-height: 18px; } } } } }
						@include fortablet {
							float: left;
							width: 33.3%;
							min-height: 245px; } } } } } } }

.showcontsblock {
	clear: both;
	padding: 25px 0;
	div {
		float: left;
		width: 48%;
		margin-right: 25px;
		&:last-of-type {
			margin-right: 0;
			@include font($lato, 14px, 400);
			line-height: 22px;
			color: $white; }
		&.imgBlock {
			margin-bottom: 25px;
			img {
				width: 100%; } }
		@include forresponsive() {
			width: 100%;
			margin-right: 0;
			&.contsBlock {
				margin-bottom: 25px; } } } }

.showcontswrapper {
	&.eps {
		.titleBox {
			&.vids {
				h2 {
					float: left;
					width: 75%;
					@include forresponsive() {
						float: none;
						width: 100%;
						margin-bottom: 15px; } }
				.views {
					float: right;
					text-align: right;
					width: 25%;
					@include forresponsive() {
						float: none;
						width: 100%;
						text-align: left; }
					.dateuploaded {
						@include font($lato, 14px, 400);
						padding-bottom: 5px; }
					.count {
						@include font($lato, 18px, 700); } } } }
		.contsBlock {
			float: none;
			width: 100%;
			@include font($lato, 14px, 400);
			line-height: 22px;
			margin-bottom: 35px; }
		.commentBox {
			max-width: 680px;
			width: 100%;
			margin: 0 auto;
			float: none;
			img {
				width: 100%;
				height: auto; } }
		.socialIconsFeeds {
			width: 100%;
			margin: 0 auto;
			float: none;
			overflow: hidden;
			.shares {
				float: left;
				@include formobile() {
					float: none;
					clear: both;
					overflow: hidden;
					margin-bottom: 10px; }
				li {
					float: left;
					margin: 10px 10px 0 0;
					&:last-of-type {
						margin-right: 0; } } }
			.links {
				float: right;
				margin-bottom: 35px;
				overflow: hidden;
				@include formobile() {
					float: none;
					clear: both; }
				li {
					float: left;
					margin-right: 10px;
					&:last-of-type {
						margin-right: 0; }
					a {
						display: block;
						width: 50px;
						height: 50px;
						position: relative;
						color: $white;
						@include transitionspeed(.3s);
						&.fbLink {
							background: $fbBlue;
							&:hover {
								background: $fbBlueDarken; } }
						&.twLink {
							background: $twBlue;
							&:hover {
								background: $twBlueDarken; } }
						&.diggLink {
							background: $blakish;
							&:hover {
								background: $lightGray; } }
						i {
							@include centerAll(); } } } } } } }

